import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: () => import('@/components/Hello')
    }
    , {
      path: '/sign',
      name: 'Hello',
      component: () => import('@/pages/Sign')
    }
  ]
})



